import React, {useState, useEffect, useContext} from 'react'
import {isEmpty, isInteger} from 'lodash-es'
import Swiper from 'react-id-swiper';
import 'swiper/css/swiper.css'
import styled from '@emotion/styled'
import {useImmer} from 'use-immer'
import cx from 'clsx'
import './style.scss'
import StoreContext from "../../contexts/store";
import moment from 'moment'
import _ from 'lodash-es'

const Status = styled.div`
      margin-top: 28px;
      display: flex;
      justify-content: center;
      border-top: 2px solid ${props => props.over ? '#38acff' : '#dfdfdf'};
      position: relative;
      
      span {
        &:before {
          content: '';
          width: 12px;
          height: 12px;
          border: 2px solid ${props => props.over ? '#38acff' : '#dfdfdf'};
          background-color: ${props => (props.active && props.over) ? '#38acff' : '#f8f8f8'};
          border-radius: 25px;
          position: absolute;
          top: -7px;
          left: 77px;
          z-index: 1000;
          transition: all 200ms ease-in;
          cursor: pointer;
          &:hover {
            background-color: #38acff;
          }
        }
      }
`


const BtnNav = styled.button`
    top: 50%;
    bottom: auto;
    height: 34px;
    width: 34px;
    border-radius: 50%;
    border: 2px solid #dfdfdf;    
    &::after {
        content: '';
        height: 16px;
        width: 16px;
    }
`

const Wrapper = styled.div`
    position: absolute;
    bottom: 4px;
    z-index: 10000;
    width: 100%;
    display: flex;
    width: 100%;
    justify-content: center;
    .flex {
        border-radius: 6px;
        padding: 5px 0 5px 0;
        max-width: ${props => props.width}px;
        background: white;
        opacity: 0.8;
    }
`

const formatSlides = (data = []) => {
    return data.map(v => ({
        value: v,
        active: false,
        over: false
    }))
}

function Timeline({width = 700}) {
    const {dispatch: dsp, results, layersIndex} = useContext(StoreContext)
    const [swiper, setSwiper] = useState(null);
    const params = {
        // autoplay: {
        //     delay: 2000,
        // },
        width: 250,
        // initialSlide: 2,
        slidesPerView: 3,
        // slidesPerGroup: 3,
        grabCursor: true,
        mousewheel: true,
        getSwiper: setSwiper,
        on: {
            slideChange(a) {
                console.log(a);
            }
        }
    }


    const goNext = () => {
        if (swiper !== null) swiper.slideNext()
    }

    const goPrev = () => {
        if (swiper !== null) swiper.slidePrev()
    }

    const goTo = (index, speed = 100) => {
        if (swiper !== null) swiper.slideTo(index - 1, speed)
    }

    const [slides, updateSides] = useImmer(formatSlides());


    useEffect(() => {
        if(!isEmpty(results)){
            let dates = _.chain(results).map(v => moment(v.date).format('X')).sort().map(v => 'T'+moment(v, 'X').format('M')).value()
            updateSides(state => formatSlides(dates))
        }
    }, [JSON.stringify(results)])


    const setSlide = (index) => {
        updateSides(state => {
            state.map((v, k) => {
                if (k === index) {
                    v.active = true
                    v.over = true
                } else if (k < index) {
                    v.active = false
                    v.over = true
                } else {
                    v.active = false
                    v.over = false
                }

            })
        })
    }


    useEffect(() => {
        if(isInteger(layersIndex)){
            dsp('setLayers', {name: results[layersIndex].layers})
        } else {
            dsp('resetLayers')
        }

        setSlide(layersIndex)

    }, [layersIndex])

    const onClick = (index) => {
        dsp('setLayersIndex', index)
        setSlide(index)
    }

    return (
        <Wrapper width={width}>
            {!isEmpty(slides) && (
                <div className="flex">
                    <div className="self-center p-2">
                        <BtnNav onClick={goPrev}><i className="icon-arrow-left22"/></BtnNav>
                    </div>
                    <Swiper {...params} >
                        {slides.map((s, k) => (
                            <div key={k} className="swiper-item">
                                <div className={cx("timestamp", {active: s.active})} onClick={e => onClick(k)}>
                                    {s.value}
                                </div>
                                <Status active={s.active} over={s.over}><span onClick={e => onClick(k)}/></Status>
                            </div>
                        ))}
                        <div className="swiper-item">
                            <div className="timestamp"/>
                            <Status/>
                        </div>
                    </Swiper>
                    <div className="self-center p-2">
                        <BtnNav onClick={goNext}><i className="icon-arrow-right22"/></BtnNav>
                    </div>
                </div>
            )}
        </Wrapper>
    )
}

export default Timeline