import React, {memo, useState, useContext, useEffect} from 'react'
import {useForm, Form, Field, FormContext} from '@ttungbmt/react.form'
import {useFetch} from "@ttungbmt/react.http";
import Box from '@ttungbmt/react.ui.box';
import {findIndex, get, isEmpty, isNil, find} from 'lodash-es'
import $ from 'jquery'

import StoreContext from "contexts/store"
import Slider from "@material-ui/core/Slider";
import Legend from "./components/Legend";
import moment from 'moment'



function Badge({title}) {
    return <Box mb={1} className="badge badge-light badge-striped badge-striped-left border-left-primary"
                textSm>{title}</Box>
}

function FormMain() {
    const {dispatch: dsp, results, layersIndex, opacity, years, folders} = useContext(StoreContext)
    const methods = useForm()

    const handleChange = (e, val) => {
        dsp('setOpacity', val)
    }

    const {data, loading} = useFetch({
        key: 'todos',
        url: '/api/dm/dm-folder-all',
        initialData: {}
    })

    const onChangeFolder = e => dsp('selectedFolder', e.target.value)

    const onXuly = (e) => {
        let value = parseInt(e.target.value),
            folder = get(find(folders, {selected: true}), 'value')

        dsp('selectedYear', value)

        $.get(`/api/dm/results?year=${value}&folder=${folder}`, ({data}) => {
            dsp('setResults', data.results)
        })
    }

    useEffect(() => {
        if (!loading) {
            dsp('setFolders', data)
        }

    }, [loading])

    useEffect(() => {
        if(!isNil(layersIndex)){
            $('#drop-result').val(get(results, layersIndex+'.value'))
        }
    }, [layersIndex])


    if (loading || !data) return null

    return (
        <Box p={1}>
            <FormContext {...methods}>
                <Form>
                    <div className="mt-2">
                        <Badge title={`Map`}/>
                        <Box>
                            <Field
                                id="drop-result"
                                as="select"
                                name="folder"
                                options={folders || []}
                                placeholder="Chn folder..."
                                onChange={onChangeFolder}
                                value={get(folders.find(v => v.selected), 'value', '')}
                            />

                            {!isEmpty(years) && (
                                <Field
                                    id="drop-result"
                                    as="select"
                                    name="year"
                                    options={years || []}
                                    placeholder="Chn nm..."
                                    onChange={onXuly}
                                    value={get(years.find(v => v.selected), 'value', '')}
                                />
                            )}

                        </Box>
                    </div>

                    {!isEmpty(results) && (
                        <div style={{maxHeight: 400}} className="overflow-auto mb-2">
                            <table className="table table-bordered table-hover">
                                <thead>
                                <tr>
                                    <td style={{fontWeight: 'bold'}}>Tn</td>
                                    <td style={{fontWeight: 'bold'}}>Thi gian</td>
                                </tr>
                                </thead>
                                <tbody>
                                {results.map((r, k) => (
                                    <tr key={k}>
                                        <td>{r.label}</td>
                                        <td>{moment(r.date).format('DD/MM/YYYY')}</td>
                                    </tr>
                                ))}
                                </tbody>
                            </table>
                        </div>

                    )}

                    <div className="form-group">
                        <label htmlFor=""> trong sut ({opacity})</label>
                        <div className="p-2">
                            <Slider value={opacity} onChange={handleChange}  defaultValue={1}  min={0} step={0.1} max={1}/>
                        </div>
                    </div>

                </Form>
            </FormContext>
            <Legend />
        </Box>


    )
}

export default memo(FormMain)
