import React, {memo, useContext} from 'react'
import styled from '@emotion/styled'
import Box from "@ttungbmt/react.ui.box";
import Badge from "./Badge";
import StoreContext from "../contexts/store";
import {isEmpty} from 'lodash-es'

const Wrapper = styled.div`
    .box-color {
        width: 20px;
        height: 20px;
        margin-right: 6px;
        border: 1px solid #c6cad0;
    }
`

function Legend(props) {
    const {layers, bounds, legends, opacity} = useContext(StoreContext)
    if(isEmpty(legends)) return null

    return (
        <Wrapper>
            <Badge>Ch gii</Badge>
            <div className="mt-2">
                {legends.map((l, k) => (
                    <div key={k} className="flex">
                        <div className="box-color" style={{backgroundColor: l.color}} ></div>
                        <div>{l.legend}</div>
                    </div>
                ))}
            </div>

        </Wrapper>
    )
}


export default memo(Legend)

