import React, {memo, useEffect, useState, useContext, useRef} from 'react'
import {MapContainer, WMSTileLayer, useLeaflet, Popup, LegendControl} from '@ttungbmt/redux-leaflet'
import {uniqid} from "@ttungbmt/utils";
import {getWMSLayers} from '@ttungbmt/geo'
import L from "leaflet"
import {http} from '@ttungbmt/react.http'
import {get, isEmpty, isInteger} from 'lodash-es'
import StoreContext from "contexts/store"
import Slider from "@material-ui/core/Slider";

function useMapClick({setPop}) {
    const {map} = useLeaflet()

    useEffect(() => {
        const showInfo = ({latlng}) => {
            let info = {
                uuid: uniqid(),
                position: [latlng.lat, latlng.lng],
                items: []
            }


            map.eachLayer(l => {
                if (l._wmsVersion) {
                    let infoUrl = l.getFeatureInfoUrl(latlng)
                    setPop(false)

                    http.get(infoUrl).then(({data}) => {
                        let props = get(data, 'features.0.properties')
                        if(props) setPop({
                            position: [latlng.lat, latlng.lng],
                            content: `Ta : ${latlng.lat.toFixed(5)}, ${latlng.lng.toFixed(5)} - Gi tr: ${isInteger(props.val) ? props.val : parseFloat(props.val).toFixed(4)}`
                        })

                    })
                }
            })

        }

        map.on('click', showInfo)

        return () => {
            map.off('click', showInfo)
        }
    }, [])
}


function MapContent() {
    const legendRef = useRef()
    const leaflet = useLeaflet()
    const {layers, bounds, legends, opacity} = useContext(StoreContext)
    const [pop, setPop] = useState()

    useMapClick({setPop})

    useEffect(() => {
        if(legendRef.current) leaflet.map.removeControl(legendRef.current)

        if(!isEmpty(legends)){
            legendRef.current = L.control.legend({
                position: 'topright',
                legends: legends.map(v => ({label: v.legend, color: v.color})),
            }).addTo(leaflet.map)
        }

    }, [JSON.stringify(legends)])

    useEffect(() => {
        const map = leaflet.map
        if(!isEmpty(bounds)) map.fitBounds(bounds)
    }, [JSON.stringify(bounds)])

    return (
        <>
            {pop && (<Popup position={pop.position}>{pop.content}</Popup>)}
            {layers && <WMSTileLayer layers={layers.name} url={'/geoserver/ows?'} transparent={true} format={'image/png'} zIndex={1000} opacity={opacity}/>}
        </>

    )
}

export default memo(MapContent)