import {hot} from 'react-hot-loader/root'
import {compose} from '@ttungbmt/redux.toolkit'
import React, {memo, useEffect, useState} from 'react'
import {Layout as LAYOUT} from "@ttungbmt/react.layout"
import {MapContainer, WMSTileLayer} from "@ttungbmt/redux-leaflet"
import Box from '@ttungbmt/react.ui.box'
import {FormControl, Nav, Form} from 'react-bootstrap'
import FormMain from './FormMain'
import MapContent from "./MapContent"
import StoreContext, {StoreProvider} from './contexts/store'
import Timeline from "./components/Timeline";
import MobileDetect from 'mobile-detect'
import {useWindowSize} from 'react-use'


const isMobile = (new MobileDetect(window.navigator.userAgent)).mobile()

function App() {
    const [loaded, setLoaded] = useState(false)
    const [sbW, setSbW] = useState(380)
    const Layout = LAYOUT.Map
    const [arr, setArr] = useState({ value: 0, previous: 0 })
    const {width} = useWindowSize()

    const configLayout = {
        aside: {
            self: {
                width: sbW
            }
        }
    }

    const onToggleSb = () => {
        if(sbW === 0) {
            setSbW(width)
        } else {
            setSbW(0)
            setLoaded(false)
            setTimeout(() => {
                setLoaded(true)
            }, 300)
        }
    }


    useEffect(() => {
        setTimeout(() => setLoaded(true), 200)

        if(isMobile) setSbW(0)
    }, [])

    return (
        <StoreProvider>
            <div className="App">
                <Layout config={configLayout} >
                    <div slot="header" style={{height: '100%'}}>
                        <Box flex height={1}>
                            <Box flex width={380} contentBetween>
                                <Layout.Brand title={`Qun l Hn hn`} />
                                <button type="button" className="cursor-pointer"><i className="icon-paragraph-justify3" onClick={onToggleSb}/></button>
                            </Box>
                            <Box flex contentBetween grow>
                                <Box>
                                    <Nav className="nav h-full content-center">
                                        <Nav.Link href="/admin/gallery"><div className="font-bold">CMS</div></Nav.Link>
                                    </Nav>
                                </Box>
                            </Box>
                        </Box>
                    </div>
                    <div slot="left-sidebar">
                        <FormMain />
                    </div>
                    <div slot="map-content" style={{height: '100%'}}>
                        <Timeline width={isMobile ? 300 : 800}/>
                        {loaded && <MapContainer><MapContent /></MapContainer>}
                    </div>
                    <div slot="right-sidebar">Right Sidebar</div>
                    <div slot="footer">Footer</div>
                </Layout>

            </div>
        </StoreProvider>

    );
}

export default compose(hot, memo)(App)
